.btn {
    height: 56px;
    width: auto;
    letter-spacing: 1px;
    font-size: 18px;
    display: inline-flex;
    align-items: center;
    background: #002d2d;
    border: 1px solid #69cf9c;
    border-radius: 35px;
    box-shadow: 0 20px 50px rgb(105 207 156 / 40%);
    color: #fff;
    padding: 0 40px;
    font: 700 16px/1 "Shopify Sans",Arial,sans-serif;
    position: relative;
    z-index: 9;
    @media (max-width: 868px) {
      height: 46px;
    }
  }

  .btn::before{
    content: "";
    display: block;
    position: absolute;
    background: linear-gradient(180deg,#002d2d,#69cf9c);
    width: 100%;
    height: 100%;
    left: 0;
    border-radius: 35px;
    z-index: -1;
    opacity: 0.45;
    animation: btnAnimation 6s infinite;
  }

  .btn:hover{
      cursor: pointer;
      background: linear-gradient(180deg,#002d2d,#69cf9c);
  }